import { MetaMaskInpageProvider } from '@metamask/inpage-provider'
import PortStream from 'extension-port-stream'
import { detect } from 'detect-browser'

const CHROME_ID = 'nkbihfbeogaeaoehlefnkodbefgpgknn'
const FIREFOX_ID = 'webextension@metamask.io'
const browser = detect()
function getMetaMaskId () {
  switch (browser && browser.name) {
    case 'chrome':
      return CHROME_ID
    case 'firefox':
      return FIREFOX_ID
    default:
      return CHROME_ID
  }
}

function createMetaMaskProvider() {
  const currentMetaMaskId = getMetaMaskId()
  const metamaskPort = chrome.runtime.connect(currentMetaMaskId)
  const pluginStream = new PortStream(metamaskPort)
  return new MetaMaskInpageProvider(pluginStream)
}

export default createMetaMaskProvider
